//驗證錯誤時出現的圖形設定
.validate-msg{
  position: relative;
  display: none;
  &::before{
    content: '';
    position: absolute;
    width: 20px;
    height: 20px;
    transform:translateX(-25px);
    background: url('../images/icons/validate-msg-icon.svg') center center;
    background-size: cover;
  }
}
